import {
  ADD_ITEM,
  REMOVE_ITEM,
  RESET_ITEMS,
  ADD_REQUEST,
} from "../constants/exitConstants";

export const exitCartReducer = (state = { exitItems: [] }, action) => {
  switch (action.type) {
    case ADD_REQUEST:
      return { loading: true };
    case ADD_ITEM:
      const item = action.payload;

      const existItem = state.exitItems.find(
        (x) => x.question === item.question
      );

      if (existItem) {
        return {
          ...state,
          loading: false,
          exitItems: state.exitItems.map((x) =>
            x.question === existItem.question ? item : x
          ),
        };
      } else {
        return {
          ...state,
          loading: false,
          exitItems: [...state.exitItems, item],
        };
      }
    case REMOVE_ITEM:
      return {
        ...state,
        exitItems: state.exitItems.filter((x) => x.question !== action.payload),
      };
    case RESET_ITEMS:
      return {
        ...state,
        exitItems: [],
      };
    default:
      return state;
  }
};
