import React, { useEffect } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { Route } from "react-router-dom";
import {
  Alert,
  AlertIcon,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
  Button,
} from "@chakra-ui/react";
import { useSelector, useDispatch } from "react-redux";
import { getAllKpi } from "../redux/actions/kpiActions";
import ReactHTMLTableToExcel from "react-html-table-to-excel";

const KPILogs = () => {
  const dispatch = useDispatch();
  const allKpi = useSelector((state) => state.allKpi);
  const { kpi, error } = allKpi;
  useEffect(() => {
    dispatch(getAllKpi());
  }, [dispatch]);
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Staff KPI" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.appraisal}>
            {error && (
              <Alert>
                <AlertIcon />
                {error}
              </Alert>
            )}
            <Button colorScheme="teal">
              <ReactHTMLTableToExcel
                id="test-table-xls-button"
                className="download-table-xls-button"
                table="table-to-xls"
                filename="KPI Log"
                sheet="tablexls"
                buttonText="Download as XLS"
              />
            </Button>
            <Table style={{ fontSize: "14px" }} id="table-to-xls">
              <TableCaption>KPI</TableCaption>
              <Thead>
                <Tr>
                  <Th>Name</Th>
                  <Th>Quarter</Th>
                  <Th>Score</Th>
                  <Th>Manager Score</Th>
                  <Th>Overall</Th>
                </Tr>
              </Thead>
              <Tbody>
                {kpi &&
                  kpi.map((item, index) => (
                    <Tr key={index}>
                      <Td>{item.user.firstname + "  " + item.user.lastname}</Td>
                      <Td>{item.quarter}</Td>
                      <Td>{item.score}</Td>
                      <Td>{item.managerscore}</Td>
                      <Td>{item.overall}</Td>
                      <Td></Td>
                    </Tr>
                  ))}
              </Tbody>
            </Table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default KPILogs;
