import React, { useState } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { Route } from "react-router-dom";
import {
  Button,
  FormControl,
  FormLabel,
  Select,
  Input,
  Textarea,
} from "@chakra-ui/react";

const LeaveRequest = () => {
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [reason, setReason] = useState("");
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Leave Request" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.appraisal}>
            <div className={styles.formGrid}>
              <FormControl id="startDate" py="2">
                <FormLabel>Start Date</FormLabel>
                <Input
                  type="date"
                  value={startDate}
                  onChange={(e) => setStartDate(e.target.value)}
                />
              </FormControl>

              <FormControl id="endDate" py="2">
                <FormLabel>End Date</FormLabel>
                <Input
                  type="date"
                  value={endDate}
                  onChange={(e) => setEndDate(e.target.value)}
                />
              </FormControl>
              <FormControl id="reason" py="2">
                <FormLabel>Reason</FormLabel>
                <Textarea
                  value={reason}
                  onChange={(e) => setReason(e.target.value)}
                />
              </FormControl>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LeaveRequest;
