import {
  MANAGER_A_FAIL,
  MANAGER_A_REQUEST,
  MANAGER_A_SUCCESS,
  MANAGER_B_FAIL,
  MANAGER_B_REQUEST,
  MANAGER_B_SUCCESS,
  MANAGER_SCORE_FAIL,
  MANAGER_SCORE_REQUEST,
  MANAGER_SCORE_RESET,
  MANAGER_SCORE_SUCCESS,
  MANAGER_PUT_FAIL,
  MANAGER_PUT_REQUEST,
  MANAGER_PUT_RESET,
  MANAGER_PUT_SUCCESS,
} from "../constants/managerConstants";

export const managerAReducer = (state = { res: [], info: {} }, action) => {
  switch (action.type) {
    case MANAGER_A_REQUEST:
      return { ...state, loading: true };
    case MANAGER_A_SUCCESS:
      return {
        loading: false,
        success: true,
        res: action.payload.data,
        info: action.payload.staff,
      };
    case MANAGER_A_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

