export const managerScoreReducer = (state = {}, action) => {
  switch (action.type) {
    case MANAGER_SCORE_REQUEST:
      return { ...state, loading: true };
    case MANAGER_SCORE_SUCCESS:
      return {
        loading: false,
        success: true,
      };
    case MANAGER_SCORE_FAIL:
      return { loading: false, error: action.payload };
    case MANAGER_SCORE_RESET:
      return {};
    default:
      return state;
  }
};

