import React, { useEffect, useState } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import {
  Button,
  Center,
  CircularProgress,
  Radio,
  RadioGroup,
  Stack,
  AlertIcon,
  Alert,
  useToast,
} from "@chakra-ui/react";
import { useSelector, useDispatch } from "react-redux";
import { Route } from "react-router-dom";
import { getMScore } from "../redux/actions/appraisalActions";
import { managerRate, getStaffB } from "../redux/actions/managerActions";
import { informHR } from "../redux/actions/mailActions";
import AppraisalStep from "../components/UI/AppraisalStep";
import { MANAGER_SCORE_RESET } from "../redux/constants/managerConstants";
import { NOTIFY_HR_RESET } from "../redux/constants/mailConstants";

const ManagerSectionB = ({ match, history }) => {
  const toast = useToast();
  const id = match.params.id;
  //   Get saved question number
  const savedIndex = JSON.parse(localStorage.getItem("index"));

  //   Get Selected Staff Appraisal
  const managerB = useSelector((state) => state.managerB);
  const { res, error, loading, info } = managerB;

  //   Get Selected Staff Appraisal
  const managerScore = useSelector((state) => state.managerScore);
  const { error: errorScore, loading: loadingScore, success } = managerScore;

  //   Fetch Score from DB
  const mScore = useSelector((state) => state.mScore);
  const { loading: loadingMyScore, error: errorMyScore, mscore } = mScore;

  const dispatch = useDispatch();
  const [index, setIndex] = useState(savedIndex ? savedIndex : 0);
  const [finish, setFinish] = useState(false);
  const [score, setScore] = useState("");
  const section = "B";
  const total = mscore && mscore / 2;

  useEffect(() => {
    dispatch(getStaffB(id, section));
  }, [dispatch, id, section]);

  const nextHandler = () => {
    const question = res && res[index] && res[index]._id;
    dispatch(managerRate(score, question));
  };

  const resetHandler = () => {
    localStorage.removeItem("index");
    setIndex(0);
  };

  const prevHandler = () => {
    // const id = res && res[index - 1] && res[index - 1]._id;
    // // dispatch(previousScore(id));
    setIndex(index - 1);
  };

  if (success) {
    dispatch({ type: MANAGER_SCORE_RESET });
    const newIndex = index + 1;
    const length = res.length;
    if (newIndex >= length) {
      setFinish(true);
      dispatch(getMScore(id));
    } else {
      localStorage.setItem("index", newIndex);
      setIndex(newIndex);
    }
  }
  // Start over
  const reset = () => {
    setFinish(false);
    localStorage.setItem("index", 0);
    setIndex(0);
  };

  const notifyHR = useSelector((state) => state.notifyHR);
  const {
    loading: loadingProceed,
    success: successProceed,
    error: errorProceed,
  } = notifyHR;
  const proceed = () => {
    dispatch(informHR(id, total));
  };

  if (successProceed) {
    dispatch({ type: NOTIFY_HR_RESET });
    localStorage.removeItem("index");
    history.push(`/myTeam`);
  }

  if (errorProceed) {
    toast({
      title: "Error",
      description: errorProceed,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: NOTIFY_HR_RESET });
    localStorage.removeItem("index");
  }
  return (
    <>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Appraisal" />
        </div>
        {loadingScore ? (
          <Center>
            <CircularProgress isIndeterminate color="green.300" />
            Submitting Response
          </Center>
        ) : (
          <div className={styles.contentSm}>
            {loading ? (
              <Center>
                <CircularProgress isIndeterminate color="green.300" />
                Fetching Section B
              </Center>
            ) : (
              <div className={styles.appraisal}>
                {error && (
                  <>
                    <Alert status="error">
                      <AlertIcon />
                      {error}
                    </Alert>
                  </>
                )}
                {errorScore && (
                  <>
                    <Alert status="error">
                      <AlertIcon />
                      {errorScore}
                    </Alert>
                  </>
                )}
                {errorProceed && (
                  <>
                    <Alert status="error">
                      <AlertIcon />
                      {errorProceed}
                    </Alert>
                  </>
                )}
                <h4>
                  <b>SECTION B: 360 DEGREE PERFORMANCE REVIEW</b>
                </h4>
                <h5>
                  <b>INSTRUCTIONS:</b>
                </h5>
                <small>
                  This form is used to evaluate supervisory, professional and
                  Contract employees. Assign a number for each rating within the
                  scale and write that number in the corresponding box. Points
                  will be totalled and averaged for overall performance score.
                </small>

                <AppraisalStep />
                {finish ? (
                  <>
                    {/* Display Score */}

                    {errorMyScore && (
                      <Alert>
                        <AlertIcon />
                        {errorMyScore}
                      </Alert>
                    )}
                    {!loadingMyScore && (
                      <>
                        <div className={styles.circle}>
                          <p>Your Score</p>
                          <h3>{total}</h3>
                        </div>
                        {!loadingProceed && (
                          <>
                            <Center>
                              <small>
                                <b>Note:</b> You will not be allowed to take
                                Section A again once you click on the proceed
                                button
                              </small>
                            </Center>
                            <Center>
                              <Button onClick={reset} colorScheme="red">
                                Start Over
                              </Button>
                              &nbsp;&nbsp;
                              <Button onClick={proceed} colorScheme="teal">
                                Notify HR
                              </Button>
                            </Center>
                          </>
                        )}
                      </>
                    )}
                  </>
                ) : (
                  <section>
                    {res && res[index] && (
                      <Button colorScheme="cyan" onClick={resetHandler}>
                        Reset Appraisal
                      </Button>
                    )}
                    <h2 style={{ fontSize: "1.5rem", color: "crimson" }}>
                      Staff Name: {info && info.firstname + " " + info.lastname}
                    </h2>
                    <h3>
                      <b>Perspective:</b>{" "}
                      {res &&
                        res[index] &&
                        res[index].question &&
                        res[index].question.perspective}
                    </h3>
                    <p>
                      <b>Objectives:</b>
                      {res &&
                        res[index] &&
                        res[index].question &&
                        res[index].question.objective}
                    </p>
                    <p>
                      <b>Measures:</b>{" "}
                      {res &&
                        res[index] &&
                        res[index].question &&
                        res[index].question.measures}
                    </p>
                    <p>
                      <b>Targets:</b>{" "}
                      {res &&
                        res[index] &&
                        res[index].question &&
                        res[index].question.target}
                    </p>
                    <p>
                      <b>Initiative:</b>{" "}
                      {res &&
                        res[index] &&
                        res[index].question &&
                        res[index].question.initiative}
                    </p>

                    <h2 style={{ fontSize: "2rem", color: "green" }}>
                      Staff Score: {res && res[index] && res[index].score}
                    </h2>
                    <br />
                    {res && res[index] && (
                      <>
                        <RadioGroup defaultValue={score} colorScheme="teal">
                          <Stack>
                            <Radio
                              value="5"
                              onChange={(e) => setScore(e.target.value)}
                            >
                              Outstanding
                            </Radio>
                            <Radio
                              value="4"
                              onChange={(e) => setScore(e.target.value)}
                            >
                              Superior
                            </Radio>
                            <Radio
                              value="3"
                              onChange={(e) => setScore(e.target.value)}
                            >
                              Fully Acceptable
                            </Radio>
                            <Radio
                              value="2"
                              onChange={(e) => setScore(e.target.value)}
                            >
                              Conditional
                            </Radio>
                            <Radio
                              value="1"
                              onChange={(e) => setScore(e.target.value)}
                            >
                              Unsatisfactory
                            </Radio>
                          </Stack>
                        </RadioGroup>
                        <br />
                        {index !== 0 && (
                          <Button colorScheme="orange" onClick={prevHandler}>
                            Previous
                          </Button>
                        )}
                        &nbsp;&nbsp;
                        <Button colorScheme="teal" onClick={nextHandler}>
                          Next
                        </Button>
                      </>
                    )}
                  </section>
                )}
              </div>
            )}
          </div>
        )}
      </div>
    </>
  );
};

export default ManagerSectionB;
