import {
  GET_MYKPI_FAIL,
  GET_MYKPI_REQUEST,
  GET_MYKPI_SUCCESS,
  GET_ALLKPI_FAIL,
  GET_ALLKPI_REQUEST,
  GET_ALLKPI_SUCCESS,
} from "../constants/kpiConstants";

export const getKpiReducer = (state = { kpi: [] }, action) => {
  switch (action.type) {
    case GET_MYKPI_REQUEST:
      return { ...state, loading: true };
    case GET_MYKPI_SUCCESS:
      return {
        loading: false,
        success: true,
        kpi: action.payload.data,
      };
    case GET_MYKPI_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const getAllKpiReducer = (state = { kpi: [] }, action) => {
  switch (action.type) {
    case GET_ALLKPI_REQUEST:
      return { ...state, loading: true };
    case GET_ALLKPI_SUCCESS:
      return {
        loading: false,
        success: true,
        kpi: action.payload.data,
      };
    case GET_ALLKPI_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
