import React, { useEffect, useState } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import {
  Button,
  Center,
  CircularProgress,
  Radio,
  RadioGroup,
  Stack,
  AlertIcon,
  Alert,
} from "@chakra-ui/react";
import { useSelector, useDispatch } from "react-redux";
import {
  answerA,
  fetchSectionA,
  previousScore,
  currentAppraisal,
  getScore,
  uploadScore,
} from "../redux/actions/appraisalActions";
import AppraisalStep from "../components/UI/AppraisalStep";
import {
  ANSWER_SECTIONA_RESET,
  PREVIOUS_SCORE_RESET,
  UPLOAD_SCORE_RESET,
} from "../redux/constants/appraisalConstants";
import { Link, Route } from "react-router-dom";
import { getCurrentScore } from "../redux/actions/invActions";
import { myStatus } from "../redux/actions/mailActions";

const SectionA = ({ history }) => {
  //   Fetch Score from DB
  const myScore = useSelector((state) => state.myScore);
  const {
    loading: loadingMyScore,
    error: errorMyScore,
    score: userScore,
  } = myScore;

  const userDetails = useSelector((state) => state.userDetails);
  const { user } = userDetails;

  //   Get saved question number
  const savedIndex = JSON.parse(localStorage.getItem("index"));

  //   Fetch Section A Questions

  const getSectionA = useSelector((state) => state.getSectionA);
  const { loading, error, sectionA } = getSectionA;

  //   Get Current Appraisal & Year
  const current = useSelector((state) => state.current);
  const { appraisal } = current;

  const kpiStatus = useSelector((state) => state.kpiStatus);
  const { status } = kpiStatus;

  const dispatch = useDispatch();
  const [index, setIndex] = useState(savedIndex ? savedIndex : 0);
  const [finish, setFinish] = useState(false);
  const [score, setScore] = useState("");
  const total = userScore && userScore / 2;
  const section = "A";
  const _qid = "AppraisalA";
  const session = appraisal && appraisal.session;
  const quarter = appraisal && appraisal.quarter;

  useEffect(() => {
    dispatch(fetchSectionA());
    dispatch(currentAppraisal());
    dispatch(myStatus());
  }, [dispatch]);

  const nextHandler = () => {
    const question = sectionA && sectionA[index] && sectionA[index]._id;
    dispatch(answerA(score, question, section, session, quarter, _qid));
  };

  const resetHandler = () => {
    localStorage.removeItem("index");
    setIndex(0);
  };

  const responseA = useSelector((state) => state.responseA);
  const { loading: loadingResponse, error: errorResponse, success } = responseA;

  if (success) {
    dispatch({ type: ANSWER_SECTIONA_RESET });

    const newIndex = index + 1;
    const length = sectionA.length;
    if (newIndex >= length) {
      setFinish(true);
      dispatch(getScore());
    } else {
      localStorage.setItem("index", newIndex);
      setIndex(newIndex);
    }
  }
  const getPreviousScore = useSelector((state) => state.getPreviousScore);
  const {
    loading: loadingScore,
    success: successScore,
    scores,
  } = getPreviousScore;

  const prevHandler = () => {
    const id = sectionA && sectionA[index - 1] && sectionA[index - 1]._id;
    dispatch(previousScore(id));
  };

  if (successScore) {
    setScore(scores && scores.score);
    dispatch({ type: PREVIOUS_SCORE_RESET });
    const newIndex = index - 1;
    setIndex(newIndex);
  }

  // Start over
  const reset = () => {
    setFinish(false);
    localStorage.setItem("index", 0);
    setIndex(0);
  };

  //   proceed to sectionb
  const uploadmyScore = useSelector((state) => state.uploadmyScore);
  const { loading: loadingProceed, success: successProceed } = uploadmyScore;
  const proceed = () => {
    dispatch(uploadScore(session, quarter, total));
  };

  if (successProceed) {
    dispatch(myStatus());
    dispatch({ type: UPLOAD_SCORE_RESET });
    dispatch(getCurrentScore(session));
    localStorage.removeItem("index");
    history.push("/appraisal/sectionB");
  }
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Appraisal" />
        </div>
        {user && user.manager === undefined ? (
          <div className={styles.contentSm}>
            <p>
              You have no manager, add your manager
              <Button colorScheme="teal" size="sm">
                <Link to="/edit/officialInformation"> Here</Link>
              </Button>
            </p>
          </div>
        ) : (
          <div className={styles.contentSm}>
            {loading ? (
              <Center>
                <CircularProgress isIndeterminate color="green.300" />
                Fetching Section A
              </Center>
            ) : (
              <div className={styles.appraisal}>
                {error && (
                  <>
                    <Alert status="error">
                      <AlertIcon />
                      {error}
                    </Alert>
                  </>
                )}
                {status && status.status !== undefined ? (
                  <>
                    <h4>You Have Completed Section A, Proceed to Section B</h4>
                    <Link to="/appraisal/sectionB">
                      <Button colorScheme="teal">Proceed to Section B</Button>
                    </Link>
                  </>
                ) : (
                  <>
                    <h4>
                      <b>SECTION A: 360 DEGREE PERFORMANCE REVIEW</b>
                    </h4>
                    <h5>
                      <b>INSTRUCTIONS:</b>
                    </h5>
                    <small>
                      This form is used to evaluate supervisory, professional
                      and Contract employees. Assign a number for each rating
                      within the scale and write that number in the
                      corresponding box. Points will be totalled and averaged
                      for overall performance score.
                    </small>

                    <AppraisalStep />
                    {loadingScore ? (
                      <Center>
                        <CircularProgress isIndeterminate color="green.300" />
                        Fetching Score
                      </Center>
                    ) : loadingResponse ? (
                      <Center>
                        <CircularProgress isIndeterminate color="green.300" />
                        Submitting Response...
                      </Center>
                    ) : (
                      <>
                        {errorResponse && (
                          <Alert status="error">
                            <AlertIcon />
                            {errorResponse}
                          </Alert>
                        )}

                        {/* Appraisal  */}
                        {finish ? (
                          <>
                            {/* Display Score */}

                            {errorMyScore && (
                              <Alert>
                                <AlertIcon />
                                {errorMyScore}
                              </Alert>
                            )}
                            {!loadingMyScore && (
                              <>
                                <div className={styles.circle}>
                                  <p>Your Score</p>
                                  <h3>{total}</h3>
                                </div>
                                {!loadingProceed && (
                                  <>
                                    <Center>
                                      <small>
                                        <b>Note:</b> You will not be allowed to
                                        take Section A again once you click on
                                        the proceed button
                                      </small>
                                    </Center>
                                    <Center>
                                      <Button onClick={reset} colorScheme="red">
                                        Start Over
                                      </Button>
                                      &nbsp;&nbsp;
                                      <Button
                                        onClick={proceed}
                                        colorScheme="teal"
                                      >
                                        Proceed to Section B
                                      </Button>
                                    </Center>
                                  </>
                                )}
                              </>
                            )}
                          </>
                        ) : (
                          <section>
                            {sectionA && sectionA[index] && (
                              <Button colorScheme="cyan" onClick={resetHandler}>
                                Reset Appraisal
                              </Button>
                            )}
                            <h3>
                              {sectionA &&
                                sectionA[index] &&
                                sectionA[index].title}
                            </h3>
                            <p>
                              {sectionA &&
                                sectionA[index] &&
                                sectionA[index].description}
                            </p>
                            <br />
                            {sectionA && sectionA[index] && (
                              <>
                                <RadioGroup
                                  defaultValue={score}
                                  colorScheme="teal"
                                >
                                  <Stack>
                                    <Radio
                                      value="5"
                                      onChange={(e) => setScore(e.target.value)}
                                    >
                                      Outstanding
                                    </Radio>
                                    <Radio
                                      value="4"
                                      onChange={(e) => setScore(e.target.value)}
                                    >
                                      Superior
                                    </Radio>
                                    <Radio
                                      value="3"
                                      onChange={(e) => setScore(e.target.value)}
                                    >
                                      Fully Acceptable
                                    </Radio>
                                    <Radio
                                      value="2"
                                      onChange={(e) => setScore(e.target.value)}
                                    >
                                      Conditional
                                    </Radio>
                                    <Radio
                                      value="1"
                                      onChange={(e) => setScore(e.target.value)}
                                    >
                                      Unsatisfactory
                                    </Radio>
                                  </Stack>
                                </RadioGroup>
                                <br />
                                {index !== 0 && (
                                  <Button
                                    colorScheme="orange"
                                    onClick={prevHandler}
                                  >
                                    Previous
                                  </Button>
                                )}
                                &nbsp;&nbsp;
                                <Button
                                  colorScheme="teal"
                                  onClick={nextHandler}
                                >
                                  Next
                                </Button>
                              </>
                            )}
                          </section>
                        )}
                      </>
                    )}
                  </>
                )}
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default SectionA;
