import {
  NOTIFY_HR_FAIL,
  NOTIFY_HR_REQUEST,
  NOTIFY_HR_RESET,
  NOTIFY_HR_SUCCESS,
  NOTIFY_MANAGER_FAIL,
  NOTIFY_MANAGER_REQUEST,
  NOTIFY_MANAGER_RESET,
  NOTIFY_MANAGER_SUCCESS,
  CHECK_STATUS_FAIL,
  CHECK_STATUS_REQUEST,
  CHECK_STATUS_SUCCESS,
} from "../constants/mailConstants";

export const notifyManagerReducer = (state = {}, action) => {
  switch (action.type) {
    case NOTIFY_MANAGER_REQUEST:
      return { ...state, loading: true };
    case NOTIFY_MANAGER_SUCCESS:
      return {
        loading: false,
        success: true,
      };
    case NOTIFY_MANAGER_FAIL:
      return { loading: false, error: action.payload };
    case NOTIFY_MANAGER_RESET:
      return {};
    default:
      return state;
  }
};
export const notifyHRReducer = (state = {}, action) => {
  switch (action.type) {
    case NOTIFY_HR_REQUEST:
      return { ...state, loading: true };
    case NOTIFY_HR_SUCCESS:
      return {
        loading: false,
        success: true,
      };
    case NOTIFY_HR_FAIL:
      return { loading: false, error: action.payload };
    case NOTIFY_HR_RESET:
      return {};
    default:
      return state;
  }
};

export const statusReducer = (state = { status: {} }, action) => {
  switch (action.type) {
    case CHECK_STATUS_REQUEST:
      return { ...state, loading: true };
    case CHECK_STATUS_SUCCESS:
      return {
        loading: false,
        success: true,
        status: action.payload.data,
      };
    case CHECK_STATUS_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
