import React, { useEffect } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { Route } from "react-router-dom";
import {
  Button,
  Table,
  TableCaption,
  Thead,
  Tr,
  Th,
  Td,
  Tbody,
  CircularProgress,
  Alert,
  AlertIcon,
  Center,
} from "@chakra-ui/react";
import { useSelector, useDispatch } from "react-redux";
import { getAllStaffs } from "../redux/actions/adminActions";
import ReactHTMLTableToExcel from "react-html-table-to-excel";

const Users = () => {
  const staff = useSelector((state) => state.staff);
  const { loading, error, staffs } = staff;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getAllStaffs());
  }, [dispatch]);
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="All Staffs" />
        </div>
        {loading ? (
          <Center>
            <CircularProgress isIndeterminate color="green.300" />
            Fetching Section A
          </Center>
        ) : (
          <div className={styles.contentSm}>
            {error && (
              <>
                <Alert status="error">
                  <AlertIcon />
                  {error}
                </Alert>
              </>
            )}
            <Button colorScheme="teal">
              <ReactHTMLTableToExcel
                id="test-table-xls-button"
                className="download-table-xls-button"
                table="table-to-xls"
                filename="Staff Database"
                sheet="tablexls"
                buttonText="Download as XLS"
              />
            </Button>

            <div className={styles.appraisal} style={{ overflow: "scroll" }}>
              <Table variant="striped" colorScheme="teal" id="table-to-xls">
                <TableCaption>Lban Attendance</TableCaption>
                <Thead>
                  <Tr>
                    <Th>First Name</Th>
                    <Th>Middle Name</Th>
                    <Th>Last Name</Th>
                    <Th>Date of Birth</Th>
                    <Th>Gender</Th>
                    <Th>Department</Th>
                    <Th>Email</Th>
                    <Th>Job Description</Th>
                    <Th>CUG</Th>
                    <Th>NIN</Th>
                    <Th>BVN</Th>
                    <Th>Bank</Th>
                    <Th>Account Number</Th>
                    <Th>Account Name</Th>
                  </Tr>
                </Thead>
                <Tbody>
                  {staffs &&
                    staffs.map((staff) => (
                      <Tr key={staff._id}>
                        <Td>{staff.firstname}</Td>
                        <Td>{staff.middlename}</Td>
                        <Td>{staff.lastname}</Td>
                        <Td>{staff.dob}</Td>
                        <Td>{staff.gender}</Td>
                        <Td>{staff.department}</Td>
                        <Td>{staff.email}</Td>
                        <Td>{staff.position}</Td>
                        <Td>{staff.cug}</Td>
                        <Td>{staff.nin}</Td>
                        <Td>{staff.bvn}</Td>
                        <Td>{staff.bank}</Td>
                        <Td>{staff.accountNumber}</Td>
                        <Td>{staff.accountName}</Td>
                      </Tr>
                    ))}
                </Tbody>
              </Table>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Users;
