import axios from "axios";
import {
  NOTIFY_MANAGER_SUCCESS,
  NOTIFY_MANAGER_REQUEST,
  NOTIFY_MANAGER_FAIL,
  NOTIFY_HR_FAIL,
  NOTIFY_HR_REQUEST,
  NOTIFY_HR_SUCCESS,
  CHECK_STATUS_SUCCESS,
  CHECK_STATUS_REQUEST,
  CHECK_STATUS_FAIL,
} from "../constants/mailConstants";

export const informManager =
  (session, quarter, total) => async (dispatch, getState) => {
    try {
      dispatch({ type: NOTIFY_MANAGER_REQUEST });

      const {
        userLogin: { userInfo },
      } = getState();

      const config = {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };
      const { data } = await axios.post(
        `/api/v1/staff/score/notify/manager`,
        { session, quarter, total },
        config
      );

      dispatch({
        type: NOTIFY_MANAGER_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: NOTIFY_MANAGER_FAIL,
        payload:
          error.response && error.response.data.error
            ? error.response.data.error
            : error.message,
      });
    }
  };

export const informHR = (id, total) => async (dispatch, getState) => {
  try {
    dispatch({ type: NOTIFY_HR_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.post(
      `/api/v1/staff/score/notify/hr`,
      { id, total },
      config
    );

    dispatch({
      type: NOTIFY_HR_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: NOTIFY_HR_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};

export const myStatus = () => async (dispatch, getState) => {
  try {
    dispatch({ type: CHECK_STATUS_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.get(`/api/v1/staff/score/status`, config);

    dispatch({
      type: CHECK_STATUS_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: CHECK_STATUS_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};
