import axios from "axios";
import {
  MANAGER_B_SUCCESS,
  MANAGER_B_REQUEST,
  MANAGER_B_FAIL,
  MANAGER_A_SUCCESS,
  MANAGER_A_REQUEST,
  MANAGER_A_FAIL,
  MANAGER_SCORE_SUCCESS,
  MANAGER_SCORE_REQUEST,
  MANAGER_SCORE_FAIL,
  MANAGER_PUT_SUCCESS,
  MANAGER_PUT_FAIL,
  MANAGER_PUT_REQUEST,
} from "../constants/managerConstants";

export const getStaffA = (id, section) => async (dispatch, getState) => {
  try {
    dispatch({ type: MANAGER_A_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.get(
      `/api/v1/staff/score/${id}/${section}`,
      config
    );

    dispatch({
      type: MANAGER_A_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: MANAGER_A_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};

