import React, { useEffect } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { Route } from "react-router-dom";
import { Alert, AlertIcon } from "@chakra-ui/alert";
import Card from "../components/UI/Card";
import { GiCheckMark, GiChart, GiChessKing } from "react-icons/gi";
import { useSelector, useDispatch } from "react-redux";
import { getKpi } from "../redux/actions/kpiActions";

const KPI = () => {
  const dispatch = useDispatch();
  const myKpi = useSelector((state) => state.myKpi);
  const { kpi, error } = myKpi;
  useEffect(() => {
    dispatch(getKpi());
  }, [dispatch]);
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="My KPI" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.appraisal}>
            {error && (
              <Alert>
                <AlertIcon />
                {error}
              </Alert>
            )}
            {kpi &&
              kpi.map((item, index) => (
                <div key={index} style={{ marginBottom: "1rem" }}>
                  <div className={styles.cardGrid}>
                    <Card
                      color={styles.purple}
                      name={`${item.quarter} Score`}
                      score={item.score}
                    >
                      <GiCheckMark />
                    </Card>
                    <Card
                      color={styles.cyan}
                      name="Manager's Rating"
                      score={item.managerscore}
                    >
                      <GiChessKing />
                    </Card>
                    <Card
                      color={styles.red}
                      name="Overall Rating"
                      score={item.overall}
                    >
                      <GiChart />
                    </Card>
                  </div>
                </div>
              ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default KPI;
