import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import styles from "./styles.module.css";
import logo from "../assets/logo.png";
import {
  Alert,
  Center,
  CircularProgress,
  AlertIcon,
  useToast,
  Button,
} from "@chakra-ui/react";
import { Link } from "react-router-dom";
import { RESET_PASSWORD_RESET } from "../redux/constants/authConstants";
import { resetPassword } from "../redux/actions/authActions";

const ResetPassword = ({ history, match }) => {
  const dispatch = useDispatch();
  const toast = useToast();
  const id = match.params.id;

  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [msg, setMsg] = useState(false);

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;
  const reset = useSelector((state) => state.reset);
  const { loading, success, error } = reset;
  const submitHandler = (e) => {
    e.preventDefault();
    if (password !== confirmPassword) {
      setMsg(true);
    } else {
      setMsg(false);
      dispatch(resetPassword(id, password));
    }
  };
  useEffect(() => {
    if (userInfo) {
      history.push("/dashboard");
    }
  }, [history, userInfo]);
  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: RESET_PASSWORD_RESET });
  }

  if (success) {
    toast({
      title: "Success",
      description: "Password Reset Success",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: RESET_PASSWORD_RESET });
    history.push("/");
  }

  return (
    <div className={styles.app}>
      <div className={styles.login}>
        <div className={styles.loginCard}>
          <div className={styles.logo}>
            <Center>
              <img src={logo} alt="Lotus Beta Analytics" />
            </Center>
          </div>
          <h4 className="text-center">Lotus Beta Analytics Nigeria</h4>
          <h6 className="text-center">Employee's Portal</h6>
          <div className={`${styles.forms}`}>
            {error && (
              <Alert status="error">
                <AlertIcon />
                {error}
              </Alert>
            )}
            {msg && (
              <Alert status="error">
                <AlertIcon />
                Password does not match
              </Alert>
            )}
            {loading ? (
              <CircularProgress isIndeterminate color="purple.300" />
            ) : (
              <>
                <form onSubmit={submitHandler}>
                  <div className={`col-md-12 ${styles.formPadding}`}>
                    {/* <label>Email Address</label> */}
                    <input
                      type="password"
                      className="form-control"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      placeholder="Enter New Password"
                    />
                  </div>
                  <div className={`col-md-12 ${styles.formPadding}`}>
                    {/* <label>Email Address</label> */}
                    <input
                      type="password"
                      className="form-control"
                      value={confirmPassword}
                      onChange={(e) => setConfirmPassword(e.target.value)}
                      placeholder="Confirm New Password"
                    />
                  </div>
                  <div className={`col-md-12 ${styles.formPadding}`}>
                    <input
                      type="submit"
                      className={`form-control btn ${styles.warning}`}
                      value="Reset Password"
                    />
                  </div>
                  <div style={{ textAlign: "center" }}>
                    <span>
                      &nbsp;Don't Have an Account?{" "}
                      <Link to="/register">
                        <Button colorScheme="orange" size="sm">
                          Register Now!
                        </Button>
                      </Link>
                    </span>
                  </div>
                  <div
                    className={`col-md-12 ${styles.formPadding} text-center`}
                  >
                    <small>
                      &copy; {new Date().getFullYear()} All Rights and Reserved
                      <br />
                      Lotus Beta Analytics Nigeria Limited
                    </small>
                  </div>
                </form>
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ResetPassword;
