import React, { useState } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { Link, Route } from "react-router-dom";
import { Button } from "@chakra-ui/button";
import {
  Alert,
  AlertIcon,
  Textarea,
  FormControl,
  Center,
  CircularProgress,
} from "@chakra-ui/react";
import questions from "../components/questions";
import emoji from "../assets/emoji.gif";
import { useSelector, useDispatch } from "react-redux";
import { addToExitCart } from "../redux/actions/exitActions";

const ExitInterview = () => {
  const [index, setIndex] = useState(0);
  const [res, setRes] = useState("");
  const dispatch = useDispatch();
  const exitCart = useSelector((state) => state.exitCart);
  const { loading, exitItems } = exitCart;
  const submitHandler = () => {
    const id = questions[index].id;
    const question = questions[index].question;
    const response = res;
    dispatch(addToExitCart(id, question, response));

    setIndex(index + 1);
    setRes("");
  };
  const prevHandler = () => {
    setIndex(index - 1);
  };
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Exit Interview" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.appraisal}>
            {loading ? (
              <Center>
                <CircularProgress isIndeterminate color="green.300" />
                Fetching Section A
              </Center>
            ) : (
              <>
                <img src={emoji} alt="Sad Emoji" width="200px" />
                <h3>{questions[index].question}</h3>
                {questions[index].response === null ? (
                  <FormControl id="reason" py="2">
                    <Textarea
                      value={res}
                      onChange={(e) => setRes(e.target.value)}
                    />
                  </FormControl>
                ) : (
                  <FormControl id="endDate" py="2">
                    {questions[index].response.map((item, i) => (
                      <>
                        <div className="inputGroup" key={i}>
                          <input
                            id={`radio${i}`}
                            name="radio"
                            type="radio"
                            value={item}
                            onChange={(e) => setRes(e.target.value)}
                          />
                          <label htmlFor={`radio${i}`}>{item}</label>
                        </div>
                      </>
                    ))}
                  </FormControl>
                )}
                {index > 0 && (
                  <Button
                    type="button"
                    colorScheme="orange"
                    onClick={prevHandler}
                  >
                    Back
                  </Button>
                )}

                <Button
                  type="button"
                  colorScheme="teal"
                  onClick={submitHandler}
                >
                  Next
                </Button>
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ExitInterview;
