import React, { useEffect } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { Route, Link } from "react-router-dom";
import {
  Box,
  Image,
  Center,
  Alert,
  AlertIcon,
  CircularProgress,
} from "@chakra-ui/react";
import { useSelector, useDispatch } from "react-redux";
import { getTeam } from "../redux/actions/adminActions";

const Team = () => {
  const dispatch = useDispatch();
  const managerTeam = useSelector((state) => state.managerTeam);
  const { loading, error, team } = managerTeam;
  useEffect(() => {
    dispatch(getTeam());
  }, [dispatch]);
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="My Team" />
        </div>
        {loading ? (
          <Center>
            <CircularProgress isIndeterminate color="green.300" />
            Fetching My Team Members
          </Center>
        ) : (
          <div className={styles.contentSm}>
            <div className={styles.appraisal}>
              {error && (
                <>
                  <Alert status="error">
                    <AlertIcon />
                    {error}
                  </Alert>
                </>
              )}
              <div className={styles.mteamGrid}>
                {team &&
                  team.map((item) => (
                    <Link to={`/appraisal/rateTeam/${item._id}`} key={item._id}>
                      <Box
                        maxW="sm"
                        borderWidth="1px"
                        borderRadius="lg"
                        overflow="hidden"
                      >
                        <Center>
                          <Image
                            src={`/uploads/${item ? item.photo : ""}`}
                            alt={item.photo}
                          />
                        </Center>

                        <Box d="flex" alignItems="baseline">
                          <Box
                            color="gray.500"
                            fontWeight="semibold"
                            letterSpacing="wide"
                            fontSize="md"
                            textTransform="uppercase"
                          >
                            {item.firstname + " " + item.lastname}
                          </Box>
                        </Box>
                        <Box d="flex" alignItems="baseline">
                          <Box
                            color="gray.500"
                            fontWeight="semibold"
                            fontSize="xs"
                          >
                            {item.department}
                          </Box>
                        </Box>
                      </Box>
                    </Link>
                  ))}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Team;
