import axios from "axios";
import { ADD_ITEM, ADD_REQUEST } from "../constants/exitConstants";

export const addToExitCart =
  (id, question, response) => async (dispatch, getState) => {
    dispatch({ type: ADD_REQUEST });
    dispatch({
      type: ADD_ITEM,
      payload: {
        course: id,
        question,
        response,
      },
    });
    localStorage.setItem(
      "exitItems",
      JSON.stringify(getState().exitCart.exitItems)
    );
  };
