import axios from "axios";
import {
  GET_MYKPI_SUCCESS,
  GET_MYKPI_REQUEST,
  GET_MYKPI_FAIL,
  GET_ALLKPI_SUCCESS,
  GET_ALLKPI_REQUEST,
  GET_ALLKPI_FAIL,
} from "../constants/kpiConstants";

export const getKpi = () => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_MYKPI_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.get(`/api/v1/staff/kpi/me`, config);

    dispatch({
      type: GET_MYKPI_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: GET_MYKPI_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};

export const getAllKpi = () => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_ALLKPI_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.get(
      `/api/v1/staff/kpi/all?sort=user,quarter`,
      config
    );

    dispatch({
      type: GET_ALLKPI_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: GET_ALLKPI_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};
